.accordion {
    display: flex;
    flex-direction: column;
    width: 40vw;
    margin: 1px;
    transition: 1s;
}

.accordion__header {
    display: flex;
    color: white;
    background-color: black;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
    border-radius: 10px 10px 0 0;
    cursor: pointer;
}

.accordion__header:hover {
    opacity: 0.9;
}

.accordion__body {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: gray;
    padding: 10px;
    border-radius: 0 0 10px 10px;
    height: 40px;
    transition: 1s;
}

.accordion__body > p {
    transition: 1s;
}